import { Component, OnInit, ViewChild } from '@angular/core';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { EstadoCitasService } from '../../../providers/Api/EstadoCitas.service';
import { AlertController, IonInfiniteScroll } from '@ionic/angular';
import { Router } from '@angular/router';
import { StatusBar } from '@ionic-native/status-bar/ngx';

@Component({
  selector: 'app-hoja',
  templateUrl: './hoja.page.html',
  styleUrls: ['./hoja.page.scss'],
})
export class HojaPage implements OnInit {

  desde = 0;
  hojas = [];
  cargado = true;
  patron = '';
 textoBuscar = '';

  @ViewChild('inifinito', {static: false}) inifinito: IonInfiniteScroll;

  constructor(
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private apiHoja: EstadoCitasService,
    private router: Router,
    private statusbar: StatusBar,
    private alertCtrl: AlertController
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {

    this.desde = 0;
    console.log('volviendo')
    this.hojas = [];
    this.inifinito.disabled = false;

    this.cargarHojas();
    this.statusbar.backgroundColorByHexString('#273238');

  }


  refrescando( event ) {
    this.desde = 0;
    this.cargarHojas().then(() => {
      event.target.complete();
    }).catch(() => {
      event.target.complete();
    })
      
  }

  async cargarHojas() {
      const info = await this.almacenamiento.obtenerToken();
      if ( info ) {

        if (info.role === 'TALLER_ROLE') {

          this.textoBuscar = 'Buscar por cliente';

        } else {
          this.textoBuscar = 'Buscar por taller';
        }
          
          this.apiHoja.buscarHojas(info.token, {desde: this.desde}).subscribe((data: any) => {
            console.log(data);
            if (data.exe) {
              this.hojas = data.response;

            } else {
              this.hojas = [];
              this.toast.toastError('No se pudo traer las hojas de vida, inténtelo más tarde.');
            }
            this.cargado = true;
          }, (err) => {
            this.cargado = true;
            this.hojas = [];
            this.toast.toastError('No se pudo traer las hojas de vida, inténtelo más tarde.');
          })
      }
  }

  async buscar(event) {
    
    if ( event.detail.value !== '') {
      this.desde = 0;
      this.patron = event.detail.value;
      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.apiHoja.buscarHojasFiltro(info.token, {desde: this.desde, patron: this.patron}).subscribe((data: any) => {
          console.log(data, 'mi data');
          if ( data.exe ){
            this.hojas = data.response;
          } else {
            this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
        })
      }
    } else {
      this.inifinito.disabled = false;
      this.cargarHojas();
    }
  }

  async loadData( event ) {
    // event.target.complete()

    this.desde = this.desde + 5;

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      if ( this.patron === '') {
        this.apiHoja.buscarHojas(info.token, {desde: this.desde}).subscribe((data: any) => {
          console.log(data);
          if (data.exe) {
            if ( data.response.length > 0  ) {
              data.response.forEach((hoja) => {
                this.hojas.push(hoja);
              })
              event.target.complete();
             } else {
              event.target.disabled = true;
              this.desde = 0;
             }
          } else {
            this.toast.toastError('No se pudo traer las hojas de vida, inténtelo más tarde.');
          }
        }, (err) => {
          this.toast.toastError('No se pudo traer las hojas de vida, inténtelo más tarde.');
        })
      } else {
        this.apiHoja.buscarHojasFiltro(info.token, {desde: this.desde, patron: this.patron}).subscribe((data: any) => {
          console.log(data, 'mi data');
          if ( data.exe ){
            if ( data.response.length > 0  ) {
              data.response.forEach((hoja) => {
                this.hojas.push(hoja);
              })
              event.target.complete();
           } else {
            event.target.disabled = true;
            this.desde = 0;
           }
          } else {
            this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo realizar la búsqueda, intentelo más tarde');
        })
      }
      
      
    }
    }
    
  navegar( hoja ) {
      if (!hoja.tipo || hoja.tipo === 'cita') {
        this.router.navigate(['/central/menu/hoja/mihoja', hoja.cita._id, 'cita'])
      } else {
        this.router.navigate(['/central/menu/hoja/mihoja', hoja._id, 'hoja'])
      }
  }

  ionViewWillLeave() {
    this.statusbar.backgroundColorByHexString('#777777');
  }

  async abrir() {
    const alert = await this.alertCtrl.create({
      header: 'Crear Hoja de vida',
      message: '¿Quieres crear una nueva hoja de vida?',
      inputs: [
        {
          type: 'text',
          placeholder: 'Nombre del cliente',
          name: 'cliente'
        }
        ,{
          type: 'text',
          placeholder: 'Plca del Vehículo',
          name: 'placa'
        }
      ],
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel'
        },
        {
          text: 'Crear Hoja',
          handler: (data) => {
              this.crear(data);
          }
        }
      ]
    })

    await alert.present();
  }

  async crear(data: any) {

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
       // tslint:disable-next-line:max-line-length
       this.apiHoja.crearHoja(info.token, {usuario: '', placa: data.placa, nusuario: data.cliente, cita: ''}).subscribe((resp: any) => {
         console.log(resp)
         if (resp.exe) {
            this.toast.toastExitoso('Hoja de vida creada');
         } else {
          this.toast.toastError('No se pudo crear la hoja de vida, intentelo más tarde');
         }
       }, (err) => {
        this.toast.toastError('No se pudo crear la hoja de vida, intentelo más tarde');
       })
    }

  }


}
